<template>
  <admin>
    <metatag :title="trans('Find Company')"></metatag>

    <page-header :back="{ to: {name: 'companies.reports'}, text: trans('Back to Reports') }">
      <template v-slot:title>{{ trans('Report Preview') }}</template>
      <template v-slot:utilities>
        <a class="dt-link text--decoration-none mr-4" @click="goToShowPage('ar')">
          <!-- <v-icon small left>mdi-delete-outline</v-icon> -->
          {{ trans('View Report in Arabic') }}
        </a>
      </template>
    </page-header>

    <template v-if="resource.loading">
      <skeleton-show></skeleton-show>
    </template>

    <template v-else>
      <v-card outlined>
        <iframe width="100%" id="iframe-preview" :src="url" frameborder="0"></iframe>
      </v-card>
    </template>
  </admin>
</template>

<script>
import $auth from '@/core/Auth/auth'

export default {
  data: () => ({
    resource: {
      loading: false,
      data: {},
    },
    url: null,
  }),

  methods: {
    getReport () {
      let id = $auth.getId()
      let customerId = this.$route.params.id
      this.url = `/best/preview/reports/overall?user_id=${id}&customer_id=${customerId}`
    },

    downloadReport () {
      window.location.href = `/reports/${this.$route.params.report}/download`
      // axios.get(
      //   `/api/v1/reports/${this.$route.params.report}/download`
      // ).then(response => {
      //   let blob = new Blob([response.data], { type: 'application/pdf' })
      //   let link = document.createElement('a')
      //   link.href = window.URL.createObjectURL(blob)
      //   link.download = `Report.pdf`
      //   link.click()
      // })
    },

    setIframeHeight () {
      this.resource.loading = true
      document.querySelector('iframe').addEventListener('load', function (e) {
        var iframe = this
        var iframeWin = iframe.contentWindow || iframe.contentDocument.parentWindow

        if (iframeWin.document.body) {
          iframe.height = iframeWin.document.documentElement.scrollHeight || iframeWin.document.body.scrollHeight
        }
      })
      this.resource.loading = false
    },

    goToShowPage (ar) {
      this.$router.push({
         name: 'reports.show',
        params: {
          id: this.$route.params.id,
          report: this.$route.params.report
        },
        query: {
          lang: 'ar'
        }
      }).catch(err => {})
      this.$router.go()

      // this.$router.go({
      //   name: 'reports.show',
      //   params: {
      //     id: this.$route.params.id,
      //     report: this.$route.params.report
      //   },
      //   query: {
      //     lang: 'ar'
      //   },
      // })
    },
  },

  mounted () {
    this.setIframeHeight()
    this.getReport()
  },
}
</script>
